.App {
  font-family: Arial, Helvetica, sans-serif;
}

.card-container {
  display: flex;
  flex-direction: row;
  justify-content: center;
  text-align: center;
  flex-wrap: wrap;
}
.card {
  border: 1.3px solid white;
}
.card .card-header {
  padding: 3%;
  color: #f0fdff;
  background-color: #229cab;
  border-bottom: 1px solid white;
}
.card:nth-child(2) .card-header {
  background-color: #1f8972;
}
.card:nth-child(3) .card-header {
  background-color: #c4554e;
}
.card:nth-child(4) .card-header {
  background-color: #282f37;
}
.card-body .card-price {
  background-color: #2cc1d5;
  color: #f0fdff;
  padding: 15%;
  font-size: 2em;
}
.card:nth-child(2) .card-body .card-price {
  background-color: #24ae8d;
}
.card:nth-child(3) .card-body .card-price {
  background-color: #f46a5f;
}
.card:nth-child(4) .card-body .card-price {
  background-color: #333a44;
}
.card-footer {
  background-color: #eeeeee;
  padding: 0.3%;
}
.card-speed {
  padding: 7%;
}

.card.selected {
  margin: 10% 0;
}
